
.gallery {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.image-container {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  max-width: min(calc(100% - 50px), 560px);
  height: auto;
  background-image: linear-gradient(#ddd, #fff);
  margin: 10px;
  padding: 10px;
  border-color: #000;
  border-width: 6px;
  border-style: solid;
}

.image {
  width: 100%;
  height: auto;
}

.image-hidden {
  width: 0%;
  height: 0%;
  visibility: hidden;
}

.image-text {
  text-align: center;
  border-color: #000;
  border-width: 4px;
  border-style: solid;
  font-weight: bold;
  font-size: max(1.9vw, 32px);
  margin: 0px;
  margin-top: 10px;
  padding: 0px;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: left;
  width: 280px;
  padding: 50px;
}

.center {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input-span {
  display: flex;
  justify-content: space-between;
  margin: 10px 0px;
  color: #fff;
}
